import { useEffect } from "react";
import ScrollReveal from "scrollreveal";

function Skills() {
  useEffect(() => {
    window.sr = ScrollReveal();
    window.sr.reveal(".skills", { delay: 500 });
  });
  return (
    <div id="Skills" className="skills">
      <h2 id="skills-title">Skills</h2>
      <div id="all-skills">
        <div id="skills">
          <img id="skill" src="./Html.png" />
          <img id="skill" src="./css.png" />
          <img id="skill" src="./js.png" />
          <img id="skill" src="./react.png" />
          <img id="skill" title="git" src="./git.png" />
        </div>
      </div>
    </div>
  );
}

export default Skills;
