import React, { useState, useEffect } from "react";
import Swiper, { Navigation, Pagination, Autoplay } from "swiper";
import "swiper/swiper-bundle.css";
import ScrollReveal from "scrollreveal";

Swiper.use([Navigation, Pagination, Autoplay]);

function Project() {
  useEffect(() => {
    const mySwiper = new Swiper(".swiper-container", {
      loop: true,
      observer: true,
      observeParents: true,
      watchSlidesVisibility: true,
      watchSlidesProgress: true,
      spaceBetween: 130,
      slidesPerView: 1,
      autoplay: 60000,

      autoplay: {
        delay: 9000,
      },
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },
      pagination: {
        el: ".swiper-pagination",
      },
      autoplay: {
        delay: 5000,
      },
    });
  });
  useEffect(() => {
    window.sr = ScrollReveal();
    window.sr.reveal("#Projects", { delay: 500 });
  });
  return (
    <div id="Projects">
      <div id="project-parent">
        <h2 id="project-title">Projects</h2>
        <div class="swiper-container">
          <div class="swiper-wrapper">
            <div class="swiper-slide">
              <h2 className="project-name">TikTok Clone</h2>
              <img className="project-img" src="./tiktok.PNG" />
              <br />
              <span className="tech-used">
                Created with: <span className="tech">HTML, CSS, React</span>{" "}
              </span>
              <p>
                A clone of the popular app TikTok. Using the Reddit API,
                <br />
                it gathers videos from popular funny subreddits.
              </p>
              <a
                target="_blank"
                href="https://henrybalassiano.github.io/Tik-Tok-Clone/"
                className="button"
              >
                Demo
              </a>
              <a
                target="_blank"
                href="https://github.com/HenryBalassiano/Tik-Tok-Clone/tree/master"
                className="button"
              >
                Code
              </a>
            </div>
            <div class="swiper-slide">
              <h2 className="project-name">Snake Game</h2>
              <img className="project-img" src="./snake.png" />
              <br />
              <span className="tech-used">
                Created with:
                <span className="tech"> HTML, CSS, Javascript</span>
              </span>
              <p>
                <br />A Snake Game built with pure Vanilla JS utilizing CSS
                Grid.
              </p>
              <a
                target="_blank"
                href="https://henrybalassiano.github.io/Snake-Game/"
                class="button"
              >
                Demo
              </a>
              <a
                target="_blank"
                href="https://github.com/HenryBalassiano/Snake-Game"
                class="button"
              >
                Code
              </a>
            </div>
          </div>
          <div class="swiper-pagination"></div>

          <div class="swiper-button-prev"></div>
          <div class="swiper-button-next"></div>
        </div>
      </div>
    </div>
  );
}

export default Project;
